<template>
  <div class="search">
    <!-- 标题 -->
    <van-nav-bar title="搜索" :fixed="true" left-arrow left-text="返回" @click-left="onClickLeft" />

    <!-- 搜索框 -->
    <van-search v-model="value" placeholder="请输入搜索关键词" show-action shape="round" @input="onSearch">
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>

    <van-row v-if="flag">
      <van-col span="8" v-for="(item, index) in carList" :key="index">
        <van-button type="default" @click="toCarInfo(item.SerialID)">{{item.AliasName}}</van-button>
      </van-col>
    </van-row>

    <van-cell-group v-else>
      <van-cell :title="item.name" v-for="(item, index) in suglist" :key="index" @click="toCarInfo(item.id)"/>
    </van-cell-group>
  </div>
</template>
<script>
import * as api from "../../api/index.js";
export default {
  data() {
    return {
      value: "",
      flag: true,  //切换显示
      suglist:[],  //搜索返回的汽车列表
      carList: [
        { AliasName: "享域", SerialID: 5644 },
        { AliasName: "思域", SerialID: 1661 },
        { AliasName: "雅阁", SerialID: 2406 },
        { AliasName: "轩逸", SerialID: 1699 },
        { AliasName: "速腾", SerialID: 1765 },
        { AliasName: "卡罗拉", SerialID: 1879 },
        { AliasName: "帕萨特", SerialID: 1796 },
        { AliasName: "宝马3系", SerialID: 1729 },
        { AliasName: "迈腾", SerialID: 1909 }
      ]
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    toCarInfo(id) {
      this.$router.push({ path: "/carinfo", query: { SerialID: id } });
    },
    async onSearch(value) {
      let data = await api.getSearch(value);
      this.suglist = data.Data.suglist
    }
  },
  watch:{
      value(newVal){
          if(newVal.length===0){
              this.flag=true
          }else{
              this.flag=false
          }
      }
  }
};
</script>
<style lang="scss">
.search {
  padding: 50px 0 50px;
  .van-nav-bar {
    height: 50px;
    line-height: 50px;
    background: #5f93ff;
    .van-nav-bar__title,
    .van-nav-bar__text,
    .van-icon {
      color: #fff;
    }
  }
  .van-search {
    background: #f5f5f5 !important;
    border-bottom: 1px solid #dcdcdc;
    .van-search__content {
      background: #fff;
    }
    .van-search__action {
      color: #999;
    }
  }
  .van-row {
    margin: 15px 0;
    text-align: center;
    button {
      margin: 5px 0;
      width: 100px;
      height: 36px;
      line-height: 36px;
      border: 1px solid #ececec;
      border-radius: 3px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
</style>